			homestay_red: `${this.$env.img1}love_add@2x.png`,
			homestay_gray: `${this.$env.img1}love_unadd@2x.png`,
			village_icon: `${this.$env.img1}village_icon@2x.png`,
			eve_1: `${this.$env.img1}eve_1@2x.png`,
